import { getImgFromURL } from '../../../../../lib';
import { Solution } from '..';
export class ControllerPixelMap {
    public img: HTMLImageElement | undefined = undefined;

    public rx: number = 20;
    public ry: number = 20;
    public rz: number = 2;
    public scaleArrow: number = 0.5;
    public colorScale = 'gray';
    public minScale = 0.0;
    public maxScale = 1.0;

    public app: Solution;
    public hostDiv: HTMLElement;
    public divMenu: HTMLElement;
    public divText: HTMLElement;

    constructor(app: Solution) {
        this.app = app;
        this.hostDiv = this.app.divMenu;
        this.initUI();
    }
    public initUI() {
        const path0 = 'https://upload.wikimedia.org/wikipedia/commons/5/57/Heightmap.png';
        const path1 = 'https://upload.wikimedia.org/wikipedia/commons/1/15/Srtm_ramp2.world.21600x10800.jpg';
        const path2 = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/heightMap/Heightmap_01.png';
        const path3 = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/heightMap/Heightmap_02.jpg';
        const path4 = 'https://raw.githubusercontent.com/NamjuLee/data/master/img/heightMap/Heightmap_03.jpg';


        const divMenu = document.createElement('div');
        this.divMenu = divMenu;
        this.hostDiv.appendChild(divMenu);
        divMenu.style.display = 'block';
        divMenu.style.position = 'absolute';
        divMenu.style.top = '0px';

        const divInput = document.createElement('input');
        // divInput.value = 'https://upload.wikimedia.org/wikipedia/commons/f/ff/Mozilla_Firefox_logo_2013.png';
        divInput.value = path2
        divInput.addEventListener('keydown', (e: KeyboardEvent) => {
            if (e.code === 'Enter') {
                // this.buildMeshByImg(divInput.value)
                this.loadImg(divInput.value)
            }
        });
        divMenu.appendChild(divInput);

        const slType = getSlider('rx', 10, 30)
        slType.slider.defaultValue = '20';
        divMenu.appendChild(slType.div);
        slType.slider.oninput = () => {
            const value = parseInt(slType.slider.value);
            this.rx = value;
            this.update();
            // this.buildMeshByImg(this.img) 
        };
        const slType2 = getSlider('ry', 10, 30)
        slType2.slider.defaultValue = '20';
        divMenu.appendChild(slType2.div);
        slType2.slider.oninput = () => {
            const value = parseInt(slType2.slider.value);
            this.ry = value;
            this.update();
            // this.buildMeshByImg(this.img) 
        };

        const slType3 = getSlider('rz', 0, 50)
        slType3.slider.defaultValue = '20';
        divMenu.appendChild(slType3.div);
        slType3.slider.oninput = () => {
            const value = parseInt(slType3.slider.value);
            this.rz = value * 0.1;
            this.update();
            // this.buildMeshByImg(this.img) 
        };


        const slType4 = getSlider('arrow', 5, 10)
        slType4.slider.defaultValue = '5';
        divMenu.appendChild(slType4.div);
        slType4.slider.oninput = () => {
            const value = parseInt(slType4.slider.value);
            this.scaleArrow = value * 0.1;
            this.update();
        };

        const check = getCheckBox();
        check.defaultChecked = true;
        check.onchange = (e: Event) => {
            // @ts-ignore
            const value = e.target.checked;
            this.app.renderer.surfaceMesh.visible = value;
        };
        divMenu.appendChild(check);

        const downloadDiv = document.createElement('button');
        downloadDiv.textContent = 'Click here to download CSV';
        downloadDiv.style.background = '#999';
        downloadDiv.onclick = () => {
            // console.log('click')
            this.app.tMap.pMap.toCSV()
        }
        divMenu.appendChild(downloadDiv);

        this.divText = document.createElement('div')
        this.divText.textContent = ''
        divMenu.appendChild(this.divText);


        const downloadJSON = document.createElement('button');
        downloadJSON.textContent = 'Click here to download JSON';
        downloadJSON.style.background = '#999';
        downloadJSON.onclick = () => {
            this.app.toJSON()
        }
        divMenu.appendChild(downloadJSON);

        const downloadOBJ = document.createElement('button');
        downloadOBJ.textContent = 'Click here to download 3D terrain(OBJ)';
        downloadOBJ.style.background = '#999';
        downloadOBJ.onclick = () => {
            this.app.toOBJ()
        }
        divMenu.appendChild(downloadOBJ);

        const colorScaleDiv = getDrawingModeRadio((v) => {
            this.colorScale = v.toLowerCase();
            this.update();
        }, ['Gray', 'False'])
        divMenu.appendChild(colorScaleDiv);


        const slType0 = getSlider('min', 0.0, 100)
        slType0.slider.defaultValue = '0';
        divMenu.appendChild(slType0.div);
        slType0.slider.oninput = () => {
            const value = parseInt(slType0.slider.value) * 0.01;
            // console.log(value);
            this.minScale = value;
            this.update();
            // this.buildMeshByImg(this.img) 
        };

        const slType1 = getSlider('max', 0.0, 100)
        slType1.slider.defaultValue = '100';
        divMenu.appendChild(slType1.div);
        slType1.slider.oninput = () => {
            const value = parseInt(slType1.slider.value) * 0.01;
            // console.log(value);
            this.maxScale = value;
            this.update();
            // this.buildMeshByImg(this.img) 
        };

    }
    public loadImg(url: string) {
        getImgFromURL(url).then((img: HTMLImageElement) => {
            if (this.img) {
                this.divMenu.removeChild(this.img)
            }

            img.onload = (ev) => {
                this.divMenu.appendChild(img)
                this.img = img;
                this.update();
            };

        });
    }
    public update() {
        // console.log(this.rx, this.ry, this.size)
        this.app.init(this.img, this.colorScale)
    }
    public destroy() {
        this.hostDiv.removeChild(this.divMenu);
    }
}

const getSlider = (title: string, min: number, max: number) => {
    // <input type="range" min="1" max="100" value="50" class="slider" id="myRange">
    const div = document.createElement('div');
    div.style.display = 'flex';

    const slider = document.createElement('input');
    slider.type = 'range';
    slider.min = `${min}`;
    slider.max = `${max}`;

    const text = document.createElement('div');
    text.textContent = title;
    text.style.color = '#aaa';
    div.appendChild(slider);
    div.appendChild(text);
    return {
        div, slider, text
    }
};

const getCheckBox = () => {
    const inputFile = document.createElement('input');
    inputFile.type = 'checkbox';
    return inputFile;
};



const getDrawingModeRadio = (fn: (v: string) => void, radioValues = ['A', 'B', 'C']) => {

    const container = document.createElement('div');
    // container.style.background = '#444';
    container.style.margin = '5px';
    // container.style.height =  '100px';
    
    const titleText = 'Scale'
    const title = document.createElement('div');
    title.textContent = titleText;
    container.appendChild(title);
    // container

    radioValues.forEach(value => {
        const label = document.createElement('label');
        label.className = 'radio-item';

        const radio = document.createElement('input');
        radio.type = 'radio';
        radio.name = 'ratio';
        radio.value = value;

        const text = document.createTextNode(value);
        label.appendChild(radio);
        label.appendChild(text);
        container.appendChild(label);

        // Add event listener to handle radio button change
        radio.addEventListener('change', function() {
            if (this.value.toLowerCase() === 'gray') {
                radio.checked = true;
                // title.textContent = titleText + ': Section'
            } else if (this.value.toLowerCase() === 'false') {
                // title.textContent = titleText + ': Line'
            // } else if (this.value.toLowerCase() === 'true') {
                // title.textContent = titleText + ': Polyline from the closest node'
            }
            fn(this.value)
        });
    });

    // Set default value
    const defaultValue = 'Gray'; // Set your default value here
    const defaultRadio = container.querySelector(`input[value="${defaultValue}"]`);
    // console.log(defaultRadio)
    if (defaultRadio) {
        // @ts-ignore
        defaultRadio.checked = true;
        // Trigger change event to update title and perform associated actions
        defaultRadio.dispatchEvent(new Event('change'));
    }

    return container;
}