import * as THREE from 'three';
import { RGBtoGray } from '../../../../lib';
import { RendererThree } from './RendererPixelMap/Base';
import { ControllerPixelMap } from './ControllerPixelMap';
import { PixelMap, Vector3 } from './PMapTerrain';
import { Renderer } from './RendererPixelMap';

import { getLookupTable, getLookupTableGrayscale } from './ColorScale';

export class Solution {
    public controller: ControllerPixelMap;
    public renderer: Renderer;

    public static colorTable: any;

    public canvas: HTMLCanvasElement;

    public divMain: HTMLElement;
    public divMenu: HTMLElement;

    tMap: TerrainMap

    public ctxLegend: CanvasRenderingContext2D;

    constructor(id: string = 'main') {




        const divMain = document.getElementById(id);
        this.divMain = divMain;

        const divMenu = document.createElement('div');
        this.divMenu = divMenu;
        divMenu.style.height = '50px';
        divMenu.style.position = 'absolute';
        divMenu.style.backgroundColor = '#f00';
        divMenu.style.zIndex = '1';
        divMain.append(divMenu);
        this.controller = new ControllerPixelMap(this);

        const divCanvas = document.createElement('div');
        divCanvas.id = 'pMap';
        divCanvas.style.width = '100%';
        divCanvas.style.height = '100%';
        divMain.append(divCanvas);

        this.renderer = new Renderer(divCanvas.id);

        this.tMap = new TerrainMap(this)

        this.controller.loadImg('https://raw.githubusercontent.com/NamjuLee/data/master/img/heightMap/Heightmap_03.jpg')

        const canvas = document.createElement('canvas');
        canvas.width = 255;
        canvas.height = 10;
        canvas.style.background = '#f0f';
        canvas.style.border = '1px #f00';

        this.controller.divMenu.appendChild(canvas);
        this.ctxLegend = canvas.getContext('2d');

    }
    public init(img, colorScale = 'gray') {
        // console.log('=============', colorScale, this.controller, this.tMap)
        Solution.colorTable = colorScale === 'gray' ? getLookupTableGrayscale() : getLookupTable()

        if (this.tMap) {

            this.tMap.colorScale = colorScale



            drawLegend(this.ctxLegend, colorScale);

            
            this.tMap.init(img, this.controller.minScale, this.controller.maxScale)

        }
    }
    public toJSON() {
        const jsonObj = this.renderer.surfaceMesh.toJSON();
        downloadBlob(JSON.stringify(jsonObj), 'terrain.json')
    }
    public toOBJ() {
        const theArray = []

        this.renderer.vertexArray.forEach((vec) => {
            const vertex = vec.map(index => index).join(' ');
            // console.log(vertex)
            theArray.push(`v ${vertex}\n`);
        });

        this.renderer.faceArray.forEach((face) => {
            const indices = face.map(index => index + 1).join(' ');
            // console.log(indices)
            theArray.push(`f ${indices}\n`);
        });

        const out = theArray.flat().join(' ')
        // console.log({ out })
        downloadOBJ(out, 'terrain.obj')
    }
    public destroy() {
        this.renderer.destroy();

        try {
            while (this.divMain !== undefined && this.divMain.lastElementChild !== undefined) {
                this.divMain.removeChild(this.divMain.lastElementChild);
            }
        } catch (error) { }
    }
}

class TerrainMap {
    public colorScale = 'gray';

    public solution: Solution;
    public pMap: PixelMap;
    constructor(solution: Solution) {
        this.solution = solution;
    }
    public init(img, minScale, maxScale ) {
        this.pMap = new PixelMap();
        this.pMap.scaleMin = minScale;
        this.pMap.scaleMax = maxScale;
        this.pMap.rx = this.solution.controller.rx;
        this.pMap.ry = this.solution.controller.ry;
        this.pMap.rz = this.solution.controller.rz;

        this.pMap.init(
            new Vector3(10, 10, 0), new Vector3(-10, -10, 0),
            this.pMap.rx, this.pMap.ry
        );

        this.solution.renderer.reset();
        this.pMap.updateImage(img);
        this.solution.renderer.buildPoint(this.pMap);
        this.solution.renderer.buildMeshByImg(this.pMap);
        this.solution.renderer.buildArrow(this.pMap, this.solution.controller.scaleArrow);
        this.solution.renderer.onClickBind = (x: number, y: number, z: number) => {
            const pixel = this.pMap.onClick(x, y, z);
            pixel.onClick();

            this.solution.controller.divText.textContent = `Elevation: ${pixel.vec.z}, Col: ${pixel.color}`;
        };
    }
}


const downloadBlob = (content, filename, contentType = 'text/json;charset=utf-8;') => {
    // Create a blob
    var blob = new Blob([content], { type: contentType });
    var url = URL.createObjectURL(blob);

    // Create a link to download it
    var pom = document.createElement('a');
    pom.href = url;
    pom.setAttribute('download', filename);
    pom.click();
}


const downloadOBJ = (objContent, filename) => {
    // Create Blob
    const blob = new Blob([objContent], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);

    // Create anchor element for downloading
    const link = document.createElement('a');
    link.href = url;
    link.download = filename;

    // Trigger download
    link.click();

    // Cleanup
    URL.revokeObjectURL(url);
}

const drawLegend = (ctx: CanvasRenderingContext2D, scale = '') => {
    ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);

    const max = 255;

    if (scale === 'gray') {
        for (let i = 0; i < max; i++) {
            ctx.beginPath();
            ctx.rect(i, 0, 1, 10);
            ctx.closePath();
            ctx.fillStyle = `rgb(${i}, ${i}, ${i})`;
            ctx.fill();
        }
    } else {
        for (let i = 0; i < max; i++) {
            const col = Solution.colorTable.getByIndex(i, 0, max).color;

            ctx.beginPath();
            ctx.rect(i, 0, 1, 10);
            ctx.closePath();
            ctx.fillStyle = `rgb(${col[0]}, ${col[1]}, ${col[2]})`;
            ctx.fill();
        }

    }
}


