// Function to map a value to a pseudocolor
export const mapValueToPseudoColor = (value, minValue = 0, maxValue = 100) => {
    // Define your pseudocolor scale here
    // This is just an example, you can adjust it as needed
    const colors = [
        [0, 0, 255],    // Blue for low values
        [0, 255, 255],  // Cyan for mid-low values
        [0, 255, 0],    // Green for mid values
        [255, 255, 0],  // Yellow for mid-high values
        [255, 0, 0]     // Red for high values
    ];

    // Normalize value to range [0, 1]
    const normalizedValue = (value - minValue) / (maxValue - minValue);

    // Scale normalized value to range [0, colors.length - 1]
    const scaledValue = normalizedValue * (colors.length - 1);

    // Find the indices of the two nearest colors
    const index1 = Math.floor(scaledValue);
    const index2 = Math.min(index1 + 1, colors.length - 1);

    // Calculate the interpolation factor
    const fraction = scaledValue - index1;

    // Interpolate between the two nearest colors
    const color1 = colors[index1];
    const color2 = colors[index2];
    const interpolatedColor = [
        Math.round(color1[0] * (1 - fraction) + color2[0] * fraction),
        Math.round(color1[1] * (1 - fraction) + color2[1] * fraction),
        Math.round(color1[2] * (1 - fraction) + color2[2] * fraction)
    ];

    return interpolatedColor;
}

export const getLookupTable = () => {
    const remap = (CValue: number, OldMin: number, OldMax: number, NewMin: number = 0, NewMax: number = 1020) => {
        return (((CValue - OldMin) * (NewMax - NewMin)) / (OldMax - OldMin)) + NewMin;
    }
    const min = 0
    const max = 1020
    const pseudoColorFromColor = { }
    const pseudoColorFromIndex = { }

    const pseudoColorTable = {
        min, max,
        colorTable: pseudoColorFromColor,
        indexTable: pseudoColorFromIndex,
        getByIndex: (i, minValue, maxValue) => {
            const reIndex = remap(i, minValue, maxValue, min, max);
            // console.log('----',reIndex)
            let index = Math.floor(reIndex)
            index = index > max ? max - 1: index;
            index = index < 0 ? 0 : index;
            // console.log({index})
            return pseudoColorFromIndex[index];
        }
    }

    for (let i = min ; i < max; i++) {
        const col = mapValueToPseudoColor(i, min, max);
        // console.log(col.join(','))
        const key = col.join(',');

        pseudoColorFromColor[key] = { index: i, color: col, };
        pseudoColorFromIndex[i] = { color: col, key: key, }

    }
    return pseudoColorTable;
}


export const getLookupTableGrayscale = () => {
    const remap = (CValue: number, OldMin: number, OldMax: number, NewMin: number = 0, NewMax: number = 255) => {
        return (((CValue - OldMin) * (NewMax - NewMin)) / (OldMax - OldMin)) + NewMin;
    }
    const min = 0
    const max = 255
    const pseudoColorFromColor = { }
    const pseudoColorFromIndex = { }

    const pseudoColorTable = {
        min, max,
        colorTable: pseudoColorFromColor,
        indexTable: pseudoColorFromIndex,
        getByIndex: (i, minValue, maxValue) => {
            const reIndex = remap(i, minValue, maxValue, min, max);
            // console.log(reIndex);
            let index = Math.floor(reIndex);
            index = index > max ? max - 1: index;
            index = index < 0 ? 0: index;
            return pseudoColorFromIndex[index];
        }
    }

    for (let i = min ; i < max; i++) {
        const col = [i, i, i];
        // console.log(col.join(','))
        const key = col.join(',');

        pseudoColorFromColor[key] = { index: i, color: col, };
        pseudoColorFromIndex[i] = { color: col, key: key, }

    }
    return pseudoColorTable;
}